import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Splitting from "splitting";
import { animateTitle1 } from "../../helpers/animateTitle";

import dan from "../../assets/images/Dan-Grossman.png";
import gabriel from "../../assets/images/Gabriel-Calzada.png";
import john from "../../assets/images/John-Mackey.png";
import katherine from "../../assets/images/Katherine-Mangu-Ward.png";
import liya from "../../assets/images/Liya-Palagashvili.png";
import rick from "../../assets/images/Rick-Rule.png";
import sam from "../../assets/images/Sam-Eckman.png";
import sloane from "../../assets/images/Sloane-Frost.png";
import wayne from "../../assets/images/Wayne-Olson.png";

import "./board-of-directors.css";

const directors = [
  {
    name: "Dan Grossman",
    position: "CHAIRMAN Retired Businessman",
    thumbnail: dan,
  },
  {
    name: "Gabriel Calzada",
    position: "Mont Pelerin Society, President",
    thumbnail: gabriel,
  },
  {
    name: "Sam Eckman",
    position: "Gibson, Dunn & Crutcher LLP, Associate",
    thumbnail: sam,
  },
  {
    name: "Sloane Frost",
    position: "Medical Home Network, Manager of Strategic Initiatives",
    thumbnail: sloane,
  },
  {
    name: "John Mackey",
    position: "Whole Foods Market, Co-Founder and Former CEO",
    thumbnail: john,
  },
  {
    name: "Katherine Mangu-Ward",
    position: "Reason Magazine, Editor in Chief",
    thumbnail: katherine,
  },
  {
    name: "Wayne Olson",
    position: "Retired investment banking executive",
    thumbnail: wayne,
  },
  {
    name: "Liya Palagashvili",
    position:
      "Senior Research Fellow, Mercatus Center, George Mason University",
    thumbnail: liya,
  },
  {
    name: "Rick Rule",
    position: "Global Resource Investments, Founder",
    thumbnail: rick,
  },
];

const BoardOfDirectors = () => {
  const titleRef = useRef();

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    Splitting();

    animateTitle1(titleRef.current);
  }, []);

  return (
    <section className="board-of-directors bg-grey">
      <div className="container">
        <h2>Students For Liberty</h2>
        <h3 ref={titleRef} data-splitting>
          Board Of Directors
        </h3>
        <div className="directors-grid-wrapper">
          {directors.map((director, i) => (
            <div key={i} className="director-card">
              <img
                src={director.thumbnail}
                alt={`${director.name}'s thumbnail`}
              />
              <p>
                <b>{director.name}</b>
              </p>
              <p>{director.position}</p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default BoardOfDirectors;
