import { useEffect, useRef, useState } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import Splitting from "splitting";
import { animateTitle1 } from "../../helpers/animateTitle";
import InteractiveChart from "../InteractiveChartOne/InteractiveChartOne";
import InteractiveChartTwo from "../InteractiveChartTwo/InteractiveChartTwo";
import Report from "../Report/Report";
import CountUp from "react-countup";

import "./finances.css";

const Finances = () => {
  const [isCountVisible, setCountVisible] = useState(false);
  const sectionRef = useRef();

  const titleRef = useRef();

  useEffect(() => {
    gsap.registerPlugin(ScrollTrigger);
    Splitting();

    animateTitle1(titleRef.current);
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      if (entries[0].isIntersecting) {
        setCountVisible(true);
      }
    });

    if (observer && sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);
  return (
    <>
      <section className="finances bg-white" ref={sectionRef}>
        <div className="container">
          <h2>Students For Liberty</h2>
          <h3 ref={titleRef} data-splitting>
            Finances
          </h3>
          <div className="finances-content-wrapper">
            <div className="finance-card">
              <h4>Revenue</h4>
              <div className="finance-card-content-wrapper">
                <div className="finances-card-header bg-yellow">
                  {isCountVisible ? (
                    <CountUp
                      start={0}
                      end={5196568}
                      delay={0.5}
                      duration={5}
                      prefix="$"
                    >
                      {({ countUpRef }) => (
                        <div>
                          <span className="number-one" ref={countUpRef} />
                        </div>
                      )}
                    </CountUp>
                  ) : (
                    <div>
                      <span className="number-one">0</span>
                    </div>
                  )}
                  <span className="icon-Icons_Revenue icomoon"></span>
                </div>
                <InteractiveChart />
              </div>
              <div className="finance-card-chart-descr">
                <ul>
                  <li>
                    <p>Contributions and grants</p>
                    <span className="percentage">89.77%</span>
                  </li>
                  <li>
                    <p>Events sponsorship/registration</p>
                    <span className="percentage">5.77%</span>
                  </li>
                  <li>
                    <p>Investment/other incomeM</p>
                    <span className="percentage">2.84%</span>
                  </li>
                  <li>
                    <p>Inkind</p> <span className="percentage">1.62%</span>
                  </li>
                </ul>
              </div>
            </div>
            <div className="finance-card">
              <h4>Expenses</h4>
              <div className="finance-card-content-wrapper ">
                <div className="finances-card-header bg-yellow">
                  {isCountVisible ? (
                    <CountUp
                      start={0}
                      end={7004850}
                      delay={0.5}
                      duration={5}
                      prefix="$"
                    >
                      {({ countUpRef }) => (
                        <div>
                          <span className="number-one" ref={countUpRef} />
                        </div>
                      )}
                    </CountUp>
                  ) : (
                    <div>
                      <span className="number-one">0</span>
                    </div>
                  )}
                  <span className="icon-Icons_Expenses icomoon"></span>
                </div>
                <InteractiveChartTwo />
              </div>

              <div className="finance-card-chart-descr">
                <ul>
                  <li>
                    <p>Programs</p>
                    <span className="percentage">82.22%</span>
                  </li>
                  <li>
                    <p>Administrative</p>
                    <span className="percentage">9.11%</span>
                  </li>
                  <li>
                    <p>Development</p>
                    <span className="percentage">8.67%</span>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
      <Report />
    </>
  );
};

export default Finances;
