import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation } from "swiper/modules";
import "swiper/css/bundle";
import "./slider-quotes.css";
import quote from "../../assets/icons/quote.png";
import connor from "../../assets/images/connor.png";
import leticia from "../../assets/images/leticia.png";
import gerreth from "../../assets/images/gerreth.png";

const quotesSlides = [
  {
    descr:
      "Students For Liberty is a life-changing opportunity for young leaders passionate about freedom. It's a network of like-minded people and professionals, a career jumpstart, and endless opportunities. I know of no other organizations that empower their volunteers to pursue what drives them and achieve more than they thought possible. I cant wait to see what my peers and I accomplish next!",
    thumbnail: connor,
    name: "Connor Sutton",
    institute: "SFL North America",
  },
  {
    descr:
      "Joining Students For Liberty kickstarted my career. I gained valuable networking, joined life-changing programs and projects, and even won a global debate competition. SFL is a transformative experience with people from all around the globe.",
    thumbnail: leticia,
    name: "Letcia Barros",
    institute: "SFL Brazil",
  },
  {
    descr:
      "I dont think I would be the same person without the experiences Ive had from SFL and the Local Coordinator program. I have met people from Canada, Europe, South America, Asia, and Africa, all of whom were open to discussing almost anything. I know of no other student organization with a community like Students For Liberty.",
    thumbnail: gerreth,
    name: "Garreth Conner",
    institute: "SFL North America",
  },
  {
    descr:
      "Students For Liberty is a life-changing opportunity for young leaders passionate about freedom. It's a network of like-minded people and professionals, a career jumpstart, and endless opportunities. I know of no other organizations that empower their volunteers to pursue what drives them and achieve more than they thought possible. I cant wait to see what my peers and I accomplish next!",
    thumbnail: connor,
    name: "Connor Sutton",
    institute: "SFL North America",
  },
  {
    descr:
      "Joining Students For Liberty kickstarted my career. I gained valuable networking, joined life-changing programs and projects, and even won a global debate competition. SFL is a transformative experience with people from all around the globe.",
    thumbnail: leticia,
    name: "Letcia Barros",
    institute: "SFL Brazil",
  },
  {
    descr:
      "I dont think I would be the same person without the experiences Ive had from SFL and the Local Coordinator program. I have met people from Canada, Europe, South America, Asia, and Africa, all of whom were open to discussing almost anything. I know of no other student organization with a community like Students For Liberty.",
    thumbnail: gerreth,
    name: "Garreth Conner",
    institute: "SFL North America",
  },
];

const SliderQuotes = () => {
  return (
    <Swiper
      modules={[Pagination, Navigation]}
      loop={true}
      centeredSlides={true}
      navigation={true}
      pagination={{ clickable: true }}
      breakpoints={{
        320: {
          slidesPerView: 1,
          spaceBetween: 20,
          centeredSlides: false,
        },
        700: {
          slidesPerView: 2,
          spaceBetween: 20,
          centeredSlides: false,
        },
        1200: {
          slidesPerView: 3.5,
          spaceBetween: 30,
        },
        1440: {
          slidesPerView: 4,
          spaceBetween: 40,
        },
      }}
    >
      {quotesSlides.map((slide, i) => (
        <SwiperSlide key={i} className="slider-quotes border-radius-20">
          <div className="quotes-content-wrapper">
            <img src={quote} className="quote-icon" />
            <p className="quotes-descr">"{slide.descr}"</p>
            <div className="quotes-speaker-info">
              <p>
                <b> {slide.name}</b>
              </p>
              <p className="institute">{slide.institute}</p>
            </div>

            <img className="quotes-speaker-thumb" src={slide.thumbnail} />
          </div>
        </SwiperSlide>
      ))}
    </Swiper>
  );
};

export default SliderQuotes;
