import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay } from "swiper/modules";
import "swiper/css/bundle";
import "./video-slider.css";

import playImage from "../../assets/icons/video-play-icon.png";

import image from "../../assets/images/video-thumb-1.png";
import image2 from "../../assets/images/video-thumb-2.png";
import image3 from "../../assets/images/video-thumb-3.png";
import image4 from "../../assets/images/video-thumb-4.png";
import image5 from "../../assets/images/video-thumb-5.png";

const videoSlides = [
  {
    thumb: image,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "211K",
  },
  {
    thumb: image2,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "81K",
  },
  {
    thumb: image3,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "231K",
  },
  {
    thumb: image4,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "121K",
  },
  {
    thumb: image5,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "291K",
  },
  {
    thumb: image3,
    link: "https://www.youtube.com/watch?v=FnLKIuUdBtg",
    title: " Lorem ipsum dolor sit amet, consectetur adipiscing elit.",
    views: "19K",
  },
];

const VideoSlider = () => {
  return (
    <Swiper
      modules={[Autoplay]}
      spaceBetween={50}
      slidesPerView={4}
      loop={true}
      centeredSlides={true}
      autoplay={{ delay: 2000, pauseOnMouseEnter: true }}
      breakpoints={{
        1300: {
          slidesPerView: 4,
        },
        1100: {
          slidesPerView: 3.5,
          spaceBetween: 25,
        },
        768: {
          slidesPerView: 2.5,
          spaceBetween: 20,
        },
        320: {
          slidesPerView: 1.5,
          spaceBetween: 20,
        },
      }}
    >
      {videoSlides.map((video, i) => (
        <SwiperSlide key={i} className="video-slide">
          <div
            className="video-slide-content"
            style={{ backgroundImage: `url(${video.thumb})` }}
          >
            <a href={video.link} target="_blank">
              <img src={playImage} />
            </a>
            <p className="video-views">
              <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 576 512">
                <path
                  fill="white"
                  d="M288 32c-80.8 0-145.5 36.8-192.6 80.6C48.6 156 17.3 208 2.5 243.7c-3.3 7.9-3.3 16.7 0 24.6C17.3 304 48.6 356 95.4 399.4C142.5 443.2 207.2 480 288 480s145.5-36.8 192.6-80.6c46.8-43.5 78.1-95.4 93-131.1c3.3-7.9 3.3-16.7 0-24.6c-14.9-35.7-46.2-87.7-93-131.1C433.5 68.8 368.8 32 288 32zM144 256a144 144 0 1 1 288 0 144 144 0 1 1 -288 0zm144-64c0 35.3-28.7 64-64 64c-7.1 0-13.9-1.2-20.3-3.3c-5.5-1.8-11.9 1.6-11.7 7.4c.3 6.9 1.3 13.8 3.2 20.7c13.7 51.2 66.4 81.6 117.6 67.9s81.6-66.4 67.9-117.6c-11.1-41.5-47.8-69.4-88.6-71.1c-5.8-.2-9.2 6.1-7.4 11.7c2.1 6.4 3.3 13.2 3.3 20.3z"
                />
              </svg>
              {video.views}
            </p>
          </div>
          <h4>{video.title}</h4>
        </SwiperSlide>
      ))}
    </Swiper>
  );
};

export default VideoSlider;
